function chatLoad(){
	var RongIMLib = require('../../rongyunIM/rongyun.js');
	var Protobuf = require('../../rongyunIM/protobuf-2.3.5.min.js');
	var RongIMClient = RongIMLib.RongIMClient;
	var timer = new Date()
	var appkey = 'pwe86ga5p4d36'
	var userId = '924756420'
	var token = 'ZU5g/RRMEU2RUhfRT95FS0roF+1VR/NPRNBHO7cp39u44UVUi937DYiUHhlkx7cZdusSShxRe04zVZ0awUjVo2Y1rIY6tpSV'
	//
	RongIMClient.init(appkey, null, {
		protobuf: Protobuf
	})
	//
	RongIMClient.setConnectionStatusListener({
		onChanged: function(status) {
			// status 
			switch (status) {
				case RongIMLib.ConnectionStatus.CONNECTED:
					console.log('');
					break;
				case RongIMLib.ConnectionStatus.CONNECTING:
					console.log('');
					break;
				case RongIMLib.ConnectionStatus.DISCONNECTED:
					console.log('');
					break;
				case RongIMLib.ConnectionStatus.KICKED_OFFLINE_BY_OTHER_CLIENT:
					console.log(', ');
					break;
				case RongIMLib.ConnectionStatus.DOMAIN_INCORRECT:
					console.log(', ');
					break;
				case RongIMLib.ConnectionStatus.NETWORK_UNAVAILABLE:
					console.log(',  reconnect ');
					break;
				default:
					console.log('', status);
					break;
			}
		}
	});
	//
	RongIMClient.setOnReceiveMessageListener({
		// 
		onReceived: function(message) {
			var messageContent = message.content;
			// 
			switch (message.messageType) {
				case RongIMClient.MessageType.TextMessage: // 
					console.log('', messageContent.content);
					break;
				case RongIMClient.MessageType.ImageMessage: // 
					console.log(' base64', messageContent.content);
					console.log(' url', messageContent.imageUri);
					break;
				case RongIMClient.MessageType.HQVoiceMessage: // 
					console.log(' type ', messageContent.type); //  aac 
					console.log(' url', messageContent.remoteUrl); // <audio src={remoteUrl} />
					console.log(' ', messageContent.duration);
					break;
				case RongIMClient.MessageType.RichContentMessage: // ()
					console.log('', messageContent.content);
					console.log(' base64', messageContent.imageUri);
					console.log(' url', messageContent.url);
					break;
				case RongIMClient.MessageType.UnknownMessage: // 
					console.log(', ', message);
					break;
				default:
					console.log('', message);
					break;
			}
		}
	});
	//
	RongIMClient.connect(token, {
		onSuccess: function(userId) {
			console.log(',  id ', userId);
			// ,  getConversationList 
		},
		onTokenIncorrect: function() {
			console.log(', : token ');
		},
		onError: function(errorCode) {
			var info = '';
			switch (errorCode) {
				case RongIMLib.ErrorCode.TIMEOUT:
					info = '';
					break;
				case RongIMLib.ConnectionState.UNACCEPTABLE_PAROTOCOL_VERSION:
					info = '';
					break;
				case RongIMLib.ConnectionState.IDENTIFIER_REJECTED:
					info = 'appkey ';
					break;
				case RongIMLib.ConnectionState.SERVER_UNAVAILABLE:
					info = '';
					break;
				default:
					info = errorCode;
					break;
			}
			console.log(', : ', info);
		}
	});
	//
	var conversationTypes = [RongIMLib.ConversationType.PRIVATE];
	var count = 150;
	RongIMClient.getInstance().getConversationList({
		onSuccess: function(list) {
			_self.linkList=list;
		},
		onError: function(error) {
			console.log('', error);
		}
	}, conversationTypes, count);
	//
	var conversationType = RongIMLib.ConversationType.PRIVATE;
	var targetId = '123';
	var timestrap = null; //  null, ,  0
	var count = 20;
	RongIMLib.RongIMClient.getInstance().getHistoryMessages(conversationType, targetId, timestrap, count, {
	  onSuccess: function(list, hasMsg) {
	    /* 
	        list: 
	        hasMsg: 
	      */
	    console.log('', list);
	      
	  },
	  onError: function(error) {
	    // 
	    console.log('', error);
	  }
	});
}